import React, { useState } from 'react';
import styles from './с.module.css'; // Импортируем стили

const IngredientsTable = ({ ingredients }) => {
  const [showAll, setShowAll] = useState(false);

  // Определяем, сколько ингредиентов показывать
  const visibleIngredients = showAll ? ingredients : ingredients.slice(0, 5);

  return (
    <div>
      {/* Таблица с ингредиентами */}
      <table className={styles.table}>
        <thead>
          <tr>
            <th>Name</th>
            <th>Quantity</th>
            <th>Unit</th>
          </tr>
        </thead>
        <tbody>
          {visibleIngredients.map((ingredient, index) => (
            <tr key={index}>
              <td>{ingredient.name}</td>
              <td>{ingredient.quantity}</td>
              <td>{ingredient.unit}</td>
            </tr>
          ))}
        </tbody>
      </table>

      {/* Кнопка для разворачивания списка */}
      {ingredients.length > 5 && (
        <button
          className={styles.button}
          onClick={() => setShowAll(!showAll)}
        >
          {showAll ? 'Свернуть список' : 'Развернуть список'}
        </button>
      )}
    </div>
  );
};

export default IngredientsTable;